import React from 'react';

import { suspects } from '../data';

import PageHeaderAndNav from './PageHeaderAndNav';
import SuspectsGallery from './SuspectsGallery';
import SuspectsTable from './SuspectsTable';
import { usePageTitle } from './shared/hooks';
import { AppWrapper } from './table-styled-components';

const SuspectsApp = () => {
  usePageTitle('January 6 Persons of Interest');

  return (
    <AppWrapper>
      <PageHeaderAndNav title="January 6 Gallery" hideGalleryLink />
      <SuspectsGallery suspects={suspects} />
    </AppWrapper>
  );
};

export const SuspectsTableApp = () => {
  usePageTitle('January 6 Persons of Interest');

  return (
    <AppWrapper>
      <PageHeaderAndNav title="January 6 Persons-of-Interest Table" />
      <SuspectsTable suspects={suspects} />
    </AppWrapper>
  );
};

export default SuspectsApp;
